'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useUser } from '@clerk/nextjs';
import {
  ArrowLeft,
  Plus,
  Edit,
  Trash2,
  Upload,
  Save,
  X,
  Loader2,
  AlertCircle,
  CheckCircle,
  Building2,
  CreditCard,
  QrCode,
  Clock,
  TrendingUp,
  Calendar,
  Percent,
  Settings
} from 'lucide-react';
import { getTokenFromCookies } from '@/api/token-user-api';
import {
  getAllBankAccounts,
  getBankAccountById,
  createBankAccount,
  updateBankAccount,
  uploadBankQRCode,
  deactivateBankAccount,
  setDailyProfitRate,
  getDailyRateStatus,
  getTodayRate,
  type BankAccount,
  type CreateBankAccountPayload,
  type UpdateBankAccountPayload,
  type DailyProfitRate,
  type DailyRateStatus,
  type SetDailyRatePayload
} from '@/api/currency-bank-api';

import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Modal } from '@/components/ui/modal';

export default function CurrencySettingsPage() {
  const router = useRouter();
  const { user, isLoaded } = useUser();

  const [token, setToken] = useState<string | null>(null);
  const [currencies, setCurrencies] = useState<CurrencyBankAccount[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  // Modal states
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [selectedCurrency, setSelectedCurrency] = useState<CurrencyBankAccount | null>(null);
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);
  const [deleteType, setDeleteType] = useState<'soft' | 'permanent'>('soft');

  // Form state
  const [formData, setFormData] = useState<CreateCurrencyBankPayload>({
    currency: '',
    currencyName: '',
    currencySymbol: '',
    bankAccounts: [
      {
        accountName: '',
        bankName: '',
        accountNumber: '',
        ifscCode: '',
        branch: '',
        upiId: ''
      }
    ],
    qrCodeUrl: '',
    qrCodeProvider: '',
    instructions: '',
    minAmount: 100,
    maxAmount: 500000,
    countryCode: '',
    processingTime: '',
    isActive: true,
    priority: 1
  });

  const [qrFile, setQrFile] = useState<File | null>(null);
  const [uploadingQR, setUploadingQR] = useState(false);

  // Get token on client side
  useEffect(() => {
    const initializeToken = async () => {
      let authToken = getTokenFromCookies();

      if (!authToken && user?.id) {
        const { generateToken: genToken, storeTokenInCookies: storeToken } = await import('@/api/token-user-api');
        const tokenResponse = await genToken(user.id);

        if (tokenResponse.success && tokenResponse.data?.token) {
          authToken = tokenResponse.data.token;
          storeToken(authToken);
        }
      }

      setToken(authToken);
    };

    if (user) {
      initializeToken();
    }
  }, [user]);

  // Fetch currencies
  useEffect(() => {
    if (!isLoaded) return;

    if (!user) {
      router.push('/login');
      return;
    }

    if (!token) return;

    fetchCurrencies();
  }, [isLoaded, user, token, router]);

  const fetchCurrencies = async () => {
    if (!token) return;

    setLoading(true);
    const response = await getAllCurrencyBanks(token);

    if (response.success && response.data) {
      setCurrencies(response.data);
    } else {
      setError(response.message || 'Failed to fetch currencies');
    }
    setLoading(false);
  };

  const handleCreateCurrency = async () => {
    if (!token) return;

    setLoading(true);
    setError('');
    setSuccess('');

    const response = await createCurrencyBank(token, formData);

    if (response.success) {
      setSuccess('Currency bank account created successfully');
      setShowCreateModal(false);
      fetchCurrencies();
      resetForm();
    } else {
      setError(response.message || 'Failed to create currency');
    }

    setLoading(false);
  };

  const handleUpdateCurrency = async () => {
    if (!token || !selectedCurrency) return;

    setLoading(true);
    setError('');
    setSuccess('');

    const updatePayload: UpdateCurrencyBankPayload = {
      bankAccounts: formData.bankAccounts,
      instructions: formData.instructions,
      processingTime: formData.processingTime,
      qrCodeProvider: formData.qrCodeProvider,
      minAmount: formData.minAmount,
      maxAmount: formData.maxAmount,
      isActive: formData.isActive,
      priority: formData.priority
    };

    const response = await updateCurrencyBank(token, selectedCurrency.currency, updatePayload);

    if (response.success) {
      setSuccess('Currency bank account updated successfully');
      setShowEditModal(false);
      fetchCurrencies();
      resetForm();
    } else {
      setError(response.message || 'Failed to update currency');
    }

    setLoading(false);
  };

  const handleUploadQR = async (currency: string) => {
    if (!token || !qrFile) return;

    setUploadingQR(true);
    setError('');
    setSuccess('');

    const response = await uploadQRCode(token, currency, qrFile);

    if (response.success) {
      setSuccess('QR code uploaded successfully');
      fetchCurrencies();
      setQrFile(null);
    } else {
      setError(response.message || 'Failed to upload QR code');
    }

    setUploadingQR(false);
  };

  const handleDelete = async () => {
    if (!token || !selectedCurrency) return;

    setLoading(true);
    setError('');
    setSuccess('');

    const response = deleteType === 'permanent'
      ? await permanentlyDeleteCurrencyBank(token, selectedCurrency.currency)
      : await deactivateCurrencyBank(token, selectedCurrency.currency);

    if (response.success) {
      setSuccess(deleteType === 'permanent'
        ? 'Currency permanently deleted'
        : 'Currency deactivated successfully');
      setShowDeleteConfirm(false);
      fetchCurrencies();
    } else {
      setError(response.message || 'Failed to delete currency');
    }

    setLoading(false);
  };

  const openEditModal = (currency: CurrencyBankAccount) => {
    setSelectedCurrency(currency);
    setFormData({
      currency: currency.currency,
      currencyName: currency.currencyName,
      currencySymbol: currency.currencySymbol,
      bankAccounts: currency.bankAccounts,
      qrCodeUrl: currency.qrCodeUrl,
      qrCodeProvider: currency.qrCodeProvider,
      instructions: currency.instructions,
      minAmount: parseInt(currency.minAmount),
      maxAmount: parseInt(currency.maxAmount),
      countryCode: currency.countryCode,
      processingTime: currency.processingTime,
      isActive: currency.isActive,
      priority: currency.priority
    });
    setShowEditModal(true);
  };

  const resetForm = () => {
    setFormData({
      currency: '',
      currencyName: '',
      currencySymbol: '',
      bankAccounts: [{
        accountName: '',
        bankName: '',
        accountNumber: '',
        ifscCode: '',
        branch: '',
        upiId: ''
      }],
      qrCodeUrl: '',
      qrCodeProvider: '',
      instructions: '',
      minAmount: 100,
      maxAmount: 500000,
      countryCode: '',
      processingTime: '',
      isActive: true,
      priority: 1
    });
    setSelectedCurrency(null);
  };

  const addBankAccount = () => {
    setFormData({
      ...formData,
      bankAccounts: [
        ...formData.bankAccounts,
        {
          accountName: '',
          bankName: '',
          accountNumber: '',
          ifscCode: '',
          branch: '',
          upiId: ''
        }
      ]
    });
  };

  const removeBankAccount = (index: number) => {
    setFormData({
      ...formData,
      bankAccounts: formData.bankAccounts.filter((_, i) => i !== index)
    });
  };

  const updateBankAccount = (index: number, field: keyof BankAccount, value: string) => {
    const updatedAccounts = [...formData.bankAccounts];
    updatedAccounts[index] = {
      ...updatedAccounts[index],
      [field]: value
    };
    setFormData({
      ...formData,
      bankAccounts: updatedAccounts
    });
  };

  if (!isLoaded || loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-background">
        <Loader2 className="w-8 h-8 animate-spin text-primary" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-background text-foreground pb-20">
      {/* Background Elements */}
      <div className="fixed top-0 left-0 w-full h-full overflow-hidden -z-10 pointer-events-none">
        <div className="absolute top-[-10%] left-[-10%] w-[40%] h-[40%] rounded-full bg-primary/5 blur-[120px]" />
        <div className="absolute bottom-[-10%] right-[-10%] w-[40%] h-[40%] rounded-full bg-accent/5 blur-[120px]" />
      </div>
      {/* Header */}
      <header className="sticky top-0 z-40 w-full border-b border-white/5 bg-background/80 backdrop-blur-xl">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <Button
                variant="ghost"
                size="sm"
                onClick={() => router.push('/dashboard')}
                className="h-10 w-10 p-0"
              >
                <ArrowLeft className="w-5 h-5" />
              </Button>
              <div className="flex items-center gap-3">
                <div className="p-2 bg-primary/10 rounded-lg">
                  <DollarSign className="w-6 h-6 text-primary" />
                </div>
                <div>
                  <h1 className="text-xl font-bold text-foreground tracking-tight">Currency Settings</h1>
                  <p className="text-xs text-muted-foreground">Manage currency bank accounts</p>
                </div>
              </div>
            </div>
            <Button
              onClick={() => setShowCreateModal(true)}
              leftIcon={<Plus className="w-4 h-4" />}
            >
              Add Currency
            </Button>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Alerts */}
        {error && (
          <div className="mb-6 bg-destructive/10 border border-destructive/20 rounded-lg p-4 flex items-start gap-3">
            <AlertCircle className="w-5 h-5 text-destructive shrink-0 mt-0.5" />
            <p className="text-sm text-destructive">{error}</p>
          </div>
        )}

        {success && (
          <div className="mb-6 bg-emerald-500/10 border border-emerald-500/20 rounded-lg p-4 flex items-start gap-3">
            <CheckCircle className="w-5 h-5 text-emerald-500 shrink-0 mt-0.5" />
            <p className="text-sm text-emerald-500">{success}</p>
          </div>
        )}

        {/* Currency Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {currencies.map((currency) => (
            <Card key={currency.id} className="hover:shadow-lg transition-all">
              <CardHeader className="pb-3">
                <div className="flex items-start justify-between">
                  <div>
                    <div className="flex items-center gap-2 mb-1">
                      <CardTitle className="text-xl">
                        {currency.currencySymbol} {currency.currency}
                      </CardTitle>
                      <Badge variant={currency.isActive ? 'success' : 'secondary'}>
                        {currency.isActive ? 'Active' : 'Inactive'}
                      </Badge>
                    </div>
                    <p className="text-sm text-muted-foreground">{currency.currencyName}</p>
                  </div>
                  <Globe className="w-6 h-6 text-muted-foreground" />
                </div>
              </CardHeader>

              <CardContent className="space-y-3">
                <div className="flex items-center gap-2 text-sm">
                  <Building2 className="w-4 h-4 text-muted-foreground" />
                  <span className="text-foreground">{currency.bankAccounts.length} Bank Account(s)</span>
                </div>
                <div className="flex items-center gap-2 text-sm">
                  <CreditCard className="w-4 h-4 text-muted-foreground" />
                  <span className="text-foreground">
                    {currency.currencySymbol}{currency.minAmount} - {currency.currencySymbol}{currency.maxAmount}
                  </span>
                </div>
                <div className="flex items-center gap-2 text-sm">
                  <Clock className="w-4 h-4 text-muted-foreground" />
                  <span className="text-foreground">{currency.processingTime}</span>
                </div>
                {currency.qrCodeUrl && (
                  <div className="flex items-center gap-2 text-sm">
                    <QrCode className="w-4 h-4 text-muted-foreground" />
                    <span className="text-foreground">{currency.qrCodeProvider}</span>
                  </div>
                )}

                <div className="flex gap-2 pt-3">
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => openEditModal(currency)}
                    leftIcon={<Edit className="w-4 h-4" />}
                    className="flex-1"
                  >
                    Edit
                  </Button>
                  <Button
                    variant="destructive"
                    size="sm"
                    onClick={() => {
                      setSelectedCurrency(currency);
                      setDeleteType('soft');
                      setShowDeleteConfirm(true);
                    }}
                    leftIcon={<Trash2 className="w-4 h-4" />}
                    className="flex-1"
                  >
                    Delete
                  </Button>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>

        {currencies.length === 0 && !loading && (
          <div className="text-center py-12">
            <DollarSign className="w-16 h-16 text-muted-foreground/20 mx-auto mb-4" />
            <h3 className="text-lg font-medium text-foreground mb-2">No currencies configured</h3>
            <p className="text-muted-foreground mb-4">Get started by adding your first currency bank account</p>
            <Button
              onClick={() => setShowCreateModal(true)}
              leftIcon={<Plus className="w-4 h-4" />}
            >
              Add Currency
            </Button>
          </div>
        )}
      </div>

      {/* Create Modal */}
      <Modal
        isOpen={showCreateModal}
        onClose={() => { setShowCreateModal(false); resetForm(); }}
        title="Create Currency Bank Account"
        maxWidth="4xl"
      >
        <div className="space-y-6 max-h-[70vh] overflow-y-auto pr-2">
          {/* Basic Info */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <Input
              label="Currency Code"
              value={formData.currency}
              onChange={(e) => setFormData({ ...formData, currency: e.target.value.toUpperCase() })}
              placeholder="INR"
            />
            <Input
              label="Currency Name"
              value={formData.currencyName}
              onChange={(e) => setFormData({ ...formData, currencyName: e.target.value })}
              placeholder="Indian Rupee"
            />
            <Input
              label="Currency Symbol"
              value={formData.currencySymbol}
              onChange={(e) => setFormData({ ...formData, currencySymbol: e.target.value })}
              placeholder="â‚¹"
            />
          </div>

          {/* Bank Accounts */}
          <div>
            <div className="flex items-center justify-between mb-4">
              <label className="block text-sm font-medium text-foreground">Bank Accounts</label>
              <Button
                variant="ghost"
                size="sm"
                onClick={addBankAccount}
                leftIcon={<Plus className="w-4 h-4" />}
              >
                Add Account
              </Button>
            </div>

            {formData.bankAccounts.map((account, index) => (
              <div key={index} className="mb-4 p-4 border border-white/10 rounded-lg bg-secondary/20">
                <div className="flex items-center justify-between mb-3">
                  <h4 className="text-sm font-medium text-foreground">Account {index + 1}</h4>
                  {formData.bankAccounts.length > 1 && (
                    <Button
                      variant="ghost"
                      size="sm"
                      onClick={() => removeBankAccount(index)}
                      className="h-8 w-8 p-0 text-destructive hover:text-destructive"
                    >
                      <X className="w-4 h-4" />
                    </Button>
                  )}
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                  <Input
                    value={account.accountName}
                    onChange={(e) => updateBankAccount(index, 'accountName', e.target.value)}
                    placeholder="Account Name"
                  />
                  <Input
                    value={account.bankName}
                    onChange={(e) => updateBankAccount(index, 'bankName', e.target.value)}
                    placeholder="Bank Name"
                  />
                  <Input
                    value={account.accountNumber}
                    onChange={(e) => updateBankAccount(index, 'accountNumber', e.target.value)}
                    placeholder="Account Number"
                  />
                  <Input
                    value={account.ifscCode}
                    onChange={(e) => updateBankAccount(index, 'ifscCode', e.target.value)}
                    placeholder="IFSC Code"
                  />
                  <Input
                    value={account.branch}
                    onChange={(e) => updateBankAccount(index, 'branch', e.target.value)}
                    placeholder="Branch"
                  />
                  <Input
                    value={account.upiId}
                    onChange={(e) => updateBankAccount(index, 'upiId', e.target.value)}
                    placeholder="UPI ID"
                  />
                </div>
              </div>
            ))}
          </div>

          {/* QR Code & Provider */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <Input
              label="QR Code URL"
              value={formData.qrCodeUrl}
              onChange={(e) => setFormData({ ...formData, qrCodeUrl: e.target.value })}
              placeholder="https://..."
            />
            <Input
              label="QR Code Provider"
              value={formData.qrCodeProvider}
              onChange={(e) => setFormData({ ...formData, qrCodeProvider: e.target.value })}
              placeholder="PhonePe, PayPay, etc."
            />
          </div>

          {/* Instructions */}
          <div>
            <label className="block text-sm font-medium text-muted-foreground mb-2">Instructions</label>
            <textarea
              value={formData.instructions}
              onChange={(e) => setFormData({ ...formData, instructions: e.target.value })}
              placeholder="Payment instructions for users..."
              rows={3}
              className="w-full px-4 py-3 bg-secondary/30 border border-white/10 rounded-lg focus:ring-2 focus:ring-primary focus:border-transparent text-foreground placeholder:text-muted-foreground"
            />
          </div>

          {/* Amounts & Settings */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <Input
              label="Min Amount"
              type="number"
              value={formData.minAmount}
              onChange={(e) => setFormData({ ...formData, minAmount: parseInt(e.target.value) })}
            />
            <Input
              label="Max Amount"
              type="number"
              value={formData.maxAmount}
              onChange={(e) => setFormData({ ...formData, maxAmount: parseInt(e.target.value) })}
            />
            <Input
              label="Country Code"
              value={formData.countryCode}
              onChange={(e) => setFormData({ ...formData, countryCode: e.target.value.toUpperCase() })}
              placeholder="IN"
            />
            <Input
              label="Processing Time"
              value={formData.processingTime}
              onChange={(e) => setFormData({ ...formData, processingTime: e.target.value })}
              placeholder="Instant to 1 hour"
            />
          </div>

          {/* Priority & Active */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <Input
              label="Priority"
              type="number"
              value={formData.priority}
              onChange={(e) => setFormData({ ...formData, priority: parseInt(e.target.value) })}
            />
            <div className="flex items-center pt-8">
              <label className="flex items-center gap-2 cursor-pointer">
                <input
                  type="checkbox"
                  checked={formData.isActive}
                  onChange={(e) => setFormData({ ...formData, isActive: e.target.checked })}
                  className="w-4 h-4 text-primary rounded focus:ring-2 focus:ring-primary"
                />
                <span className="text-sm font-medium text-foreground">Active</span>
              </label>
            </div>
          </div>
        </div>

        <div className="flex justify-end gap-3 mt-6 pt-4 border-t border-white/10">
          <Button
            variant="outline"
            onClick={() => { setShowCreateModal(false); resetForm(); }}
          >
            Cancel
          </Button>
          <Button
            onClick={handleCreateCurrency}
            disabled={loading}
            isLoading={loading}
            leftIcon={!loading ? <Save className="w-4 h-4" /> : undefined}
          >
            Create Currency
          </Button>
        </div>
      </Modal>

      {/* Edit Modal */}
      <Modal
        isOpen={showEditModal && !!selectedCurrency}
        onClose={() => { setShowEditModal(false); resetForm(); }}
        title={`Edit ${selectedCurrency?.currency}`}
        maxWidth="4xl"
      >
        {selectedCurrency && (
          <div className="space-y-6 max-h-[70vh] overflow-y-auto pr-2">
            {/* Upload QR Code */}
            <div className="bg-primary/10 border border-primary/20 rounded-lg p-4">
              <label className="block text-sm font-medium text-foreground mb-2">Upload New QR Code</label>
              <div className="flex items-center gap-3">
                <input
                  type="file"
                  accept="image/*"
                  onChange={(e) => setQrFile(e.target.files?.[0] || null)}
                  className="flex-1 text-sm text-foreground"
                />
                <Button
                  onClick={() => handleUploadQR(selectedCurrency.currency)}
                  disabled={!qrFile || uploadingQR}
                  isLoading={uploadingQR}
                  leftIcon={!uploadingQR ? <Upload className="w-4 h-4" /> : undefined}
                >
                  Upload
                </Button>
              </div>
              {selectedCurrency.qrCodeUrl && (
                <div className="mt-3">
                  <img src={selectedCurrency.qrCodeUrl} alt="QR Code" className="w-32 h-32 object-contain border border-white/10 rounded" />
                </div>
              )}
            </div>

            {/* Bank Accounts - same as Create Modal */}
            <div>
              <div className="flex items-center justify-between mb-4">
                <label className="block text-sm font-medium text-foreground">Bank Accounts</label>
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={addBankAccount}
                  leftIcon={<Plus className="w-4 h-4" />}
                >
                  Add Account
                </Button>
              </div>

              {formData.bankAccounts.map((account, index) => (
                <div key={index} className="mb-4 p-4 border border-white/10 rounded-lg bg-secondary/20">
                  <div className="flex items-center justify-between mb-3">
                    <h4 className="text-sm font-medium text-foreground">Account {index + 1}</h4>
                    {formData.bankAccounts.length > 1 && (
                      <Button
                        variant="ghost"
                        size="sm"
                        onClick={() => removeBankAccount(index)}
                        className="h-8 w-8 p-0 text-destructive hover:text-destructive"
                      >
                        <X className="w-4 h-4" />
                      </Button>
                    )}
                  </div>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                    <Input
                      value={account.accountName}
                      onChange={(e) => updateBankAccount(index, 'accountName', e.target.value)}
                      placeholder="Account Name"
                    />
                    <Input
                      value={account.bankName}
                      onChange={(e) => updateBankAccount(index, 'bankName', e.target.value)}
                      placeholder="Bank Name"
                    />
                    <Input
                      value={account.accountNumber}
                      onChange={(e) => updateBankAccount(index, 'accountNumber', e.target.value)}
                      placeholder="Account Number"
                    />
                    <Input
                      value={account.ifscCode}
                      onChange={(e) => updateBankAccount(index, 'ifscCode', e.target.value)}
                      placeholder="IFSC Code"
                    />
                    <Input
                      value={account.branch}
                      onChange={(e) => updateBankAccount(index, 'branch', e.target.value)}
                      placeholder="Branch"
                    />
                    <Input
                      value={account.upiId}
                      onChange={(e) => updateBankAccount(index, 'upiId', e.target.value)}
                      placeholder="UPI ID"
                    />
                  </div>
                </div>
              ))}
            </div>

            {/* QR Provider & Instructions */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <Input
                label="QR Code Provider"
                value={formData.qrCodeProvider}
                onChange={(e) => setFormData({ ...formData, qrCodeProvider: e.target.value })}
              />
              <Input
                label="Processing Time"
                value={formData.processingTime}
                onChange={(e) => setFormData({ ...formData, processingTime: e.target.value })}
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-muted-foreground mb-2">Instructions</label>
              <textarea
                value={formData.instructions}
                onChange={(e) => setFormData({ ...formData, instructions: e.target.value })}
                rows={3}
                className="w-full px-4 py-3 bg-secondary/30 border border-white/10 rounded-lg focus:ring-2 focus:ring-primary focus:border-transparent text-foreground placeholder:text-muted-foreground"
              />
            </div>

            {/* Amounts & Settings */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <Input
                label="Min Amount"
                type="number"
                value={formData.minAmount}
                onChange={(e) => setFormData({ ...formData, minAmount: parseInt(e.target.value) })}
              />
              <Input
                label="Max Amount"
                type="number"
                value={formData.maxAmount}
                onChange={(e) => setFormData({ ...formData, maxAmount: parseInt(e.target.value) })}
              />
              <Input
                label="Priority"
                type="number"
                value={formData.priority}
                onChange={(e) => setFormData({ ...formData, priority: parseInt(e.target.value) })}
              />
              <div className="flex items-center pt-8">
                <label className="flex items-center gap-2 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={formData.isActive}
                    onChange={(e) => setFormData({ ...formData, isActive: e.target.checked })}
                    className="w-4 h-4 text-primary rounded focus:ring-2 focus:ring-primary"
                  />
                  <span className="text-sm font-medium text-foreground">Active</span>
                </label>
              </div>
            </div>
          </div>
        )}

        <div className="flex justify-end gap-3 mt-6 pt-4 border-t border-white/10">
          <Button
            variant="outline"
            onClick={() => { setShowEditModal(false); resetForm(); }}
          >
            Cancel
          </Button>
          <Button
            onClick={handleUpdateCurrency}
            disabled={loading}
            isLoading={loading}
            leftIcon={!loading ? <Save className="w-4 h-4" /> : undefined}
          >
            Update Currency
          </Button>
        </div>
      </Modal>

      {/* Delete Confirmation Modal */}
      <Modal
        isOpen={showDeleteConfirm && !!selectedCurrency}
        onClose={() => setShowDeleteConfirm(false)}
        title="Delete Currency"
        maxWidth="md"
      >
        {selectedCurrency && (
          <>
            <div className="flex items-center gap-3 mb-6">
              <div className="w-12 h-12 bg-destructive/10 rounded-full flex items-center justify-center">
                <AlertCircle className="w-6 h-6 text-destructive" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Are you sure you want to delete {selectedCurrency.currency}?</p>
              </div>
            </div>

            <div className="mb-6 space-y-3">
              <label className="flex items-center gap-3 cursor-pointer p-3 rounded-lg border border-white/10 hover:bg-secondary/20 transition-colors">
                <input
                  type="radio"
                  checked={deleteType === 'soft'}
                  onChange={() => setDeleteType('soft')}
                  className="w-4 h-4 text-primary"
                />
                <div>
                  <span className="text-sm font-medium text-foreground block">Deactivate</span>
                  <span className="text-xs text-muted-foreground">Can be restored later</span>
                </div>
              </label>
              <label className="flex items-center gap-3 cursor-pointer p-3 rounded-lg border border-destructive/20 hover:bg-destructive/5 transition-colors">
                <input
                  type="radio"
                  checked={deleteType === 'permanent'}
                  onChange={() => setDeleteType('permanent')}
                  className="w-4 h-4 text-destructive"
                />
                <div>
                  <span className="text-sm font-medium text-destructive block">Permanently delete</span>
                  <span className="text-xs text-muted-foreground">Cannot be undone</span>
                </div>
              </label>
            </div>

            <div className="flex gap-3">
              <Button
                variant="outline"
                onClick={() => setShowDeleteConfirm(false)}
                className="flex-1"
              >
                Cancel
              </Button>
              <Button
                variant="destructive"
                onClick={handleDelete}
                disabled={loading}
                isLoading={loading}
                leftIcon={!loading ? <Trash2 className="w-4 h-4" /> : undefined}
                className="flex-1"
              >
                {deleteType === 'permanent' ? 'Delete Forever' : 'Deactivate'}
              </Button>
            </div>
          </>
        )}
      </Modal>
    </div>
  );
}
